.generator {
  border-collapse: collapse;
  border: 1px solid #000;
  margin: 0 auto;
  display: table;
  position: relative;
}

.controls {
  width: 280px;
  vertical-align: top;
  background: #e9ecef;
  padding-top: 10px;
  display: table-cell;
}

.view {
  width: min(85vh, 50vw);
  vertical-align: top;
  padding: 10px 5px;
  display: table-cell;
}

.generator input, .generator select, .generator label {
  font-family: Ubuntu;
  font-size: 18px;
}

.view canvas {
  width: 100%;
  aspect-ratio: 1 / 1;
}

.centered {
  text-align: center !important;
}

.no-top-padding {
  padding-top: 0 !important;
}

.no-margin {
  margin: 0;
}

.drag-drop {
  color: #0062cc;
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
  background: #e9ecef88;
  font-size: 2.5em;
  position: absolute;
  inset: 0;
}

.drag-drop-table {
  height: 100%;
  width: 100%;
  display: table;
}

.drag-drop-table div {
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}

@media (max-width: 600px) {
  .generator {
    display: block;
  }

  .view, .controls {
    width: auto;
    display: block;
  }

  .view canvas {
    width: 100%;
    aspect-ratio: 1 / 1;
    max-width: 600px !important;
    max-height: 600px !important;
  }
}

